const styles = theme => ({
  root: {
    width: '100%',
    height: '100%',
    overflow: 'hidden'
  },
  // Fab button icon
  'portal-calendar-event-fab__icon': {
    color: theme.palette.secondary.contrastText
  }
});

export default styles;



